<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">EDIT SQUARE FEET</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">NAME</label>
            <div class="col-md-3">
              <fg-input type="text" :value="squareFeet.name" v-model="squareFeet.name"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">SIZE</label>
            <div class="col-md-3">
              <fg-input type="text" :value="squareFeet.sqft" v-model="squareFeet.sqft"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">STATUS</label>
            <div class="col-md-3">
              <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="ACTIVE">ACTIVE</n-radio>
              <n-radio
                class="col-md-1 display-inline"
                v-model="enabledRadio"
                label="INACTIVE"
              >INACTIVE</n-radio>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-5">
              <n-button
                id="btnUpdate"
                class="button"
                type="info"
                @click.native="radio_status(enabledRadio); put(); handleUpdate();"
              >UPDATE</n-button>
              <a class="cancel">Cancel</a>
              <!-- <n-button class="button" type="danger">CANCEL</n-button> -->
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import { Radio } from "src/components";
import swal from 'sweetalert2'

export default {
  components: {
    [Radio.name]: Radio
  },
  data() {
    return {
      id: this.$route.params.id,
      squareFeet: {
        name: "",
        sqft: "",
        status: "INACTIVE"
      },
      enabledRadio: "INACTIVE",
      key_data:{
          name:"",
          sqft:"",
          status:""
      }
    };
  },
  methods: {
    //update the data to database
    put() {
      this.$http.put('https://admindashboard-b2ab9.firebaseio.com/squareFeet/'+ this.id +'.json',this.squareFeet)
    },
    radio_status(er){
      if (er == 'ACTIVE'){
        this.squareFeet.status = 'ACTIVE'; 
      }
      else {
        this.squareFeet.status = 'INACTIVE';
      }
    },
    handleUpdate() {
        swal({
          title: `You updated ${this.squareFeet.name}`,
          buttonsStyling: false,
          type: 'success',
          confirmButtonClass: 'btn btn-success btn-fill'
        })
    }
  },
  created(){
    this.$http.get('https://admindashboard-b2ab9.firebaseio.com/squareFeet.json').then(function(data){
      return data.json();
    }).then(function(data){
      //pass data to key_data object
      this.key_data = data
      //set the fields accordingly with the unique id
      this.squareFeet.name = this.key_data[this.id].name
      this.squareFeet.sqft = this.key_data[this.id].sqft
      this.squareFeet.status = this.key_data[this.id].status
        
      //toggle radio button
      if ( this.squareFeet.status == 'ACTIVE'){
        this.enabledRadio = 'ACTIVE'
      }else{
        this.enabledRadio = 'INACTIVE'
      }
    })
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel {
  /* display: inline; */
  margin-left: 20px;
  color: red;
  text-decoration: underline !important;
}
</style>
